.modal {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(226, 226, 226, 0.8);
  position: fixed;
  top: 0;
  z-index: 5;
  transition: background-color 400ms ease;
}
.modal__content {
  background-color: #fff;
  align-self: center;
  padding: 0 1rem;
  width: 80%;
  position: relative;
  border: 3px solid #635959;
  border-radius: 3px;
  align-items: center;
  box-shadow: 0.1rem 0.1rem #e2e2e2;
}
.modal__content__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #635959;
  padding: 0.5rem 0;
}

.btn {
  height: 3rem;
  border-radius: 3px;
  padding: 15px;
  border: 2px solid #ff5733;
  color: #ff5733;
  font-weight: bold;
  background-color: #fff;
}
.btn:hover {
  cursor: pointer;
  background-color: #ff5733;
  color: #fff;
}

